import React from 'react';
import { func, shape, string } from 'prop-types';

const ThemeTool = function (props) {
  const { theme, onTheme } = props;
  const onChange = (ev) => {
    const { value } = ev.target;
    const key = ev.target.name;
    onTheme({ ...theme, [key]: value });
  };
  return (
    <>
      <p>
        <span>更改颜色</span>
        <input type="color" name="color" value={theme.color} onChange={onChange} />
      </p>
      <p>
        <span>更改背景</span>
        <input type="color" name="background" value={theme.background} onChange={onChange} />
      </p>
    </>
  );
};
ThemeTool.propTypes = {
  theme: shape({
    color: string.isRequired,
    background: string.isRequired,
  }).isRequired,
  onTheme: func.isRequired,
};
export default ThemeTool;
