import React from 'react';
import { Provider } from '@/utils/theme';
import TestTheme from './TestTheme';
import { ThemeTool } from '@/components';

class ThemeShow extends React.PureComponent {
  constructor (props) {
    super(props);
    this.state = {
      theme: {
        color: '#fff',
        background: '#333',
      },
    };
  }

  onTheme = (value) => {
    this.setState((state) => ({ ...state, theme: value }));
  };

  render () {
    const { theme } = this.state;
    return (
      <>
        <Provider value={theme}>
          <TestTheme />
        </Provider>
        <ThemeTool theme={theme} onTheme={this.onTheme} />
      </>
    );
  }
}
export default ThemeShow;
